// Настройки Susy
$susy: (
  columns: 12,
  gutters: 1/5,
  math: fluid,
  output: float,
  gutter-position: split
);



// примеры Breakpoint
$wide: 1200px;
$desktop: 1024px 1200px;
$tablet: max-width 1024px;
$mobile: max-width 768px;
$mobile-2: max-width 480px;
$landscape: (max-height 600px) (orientation portrait);
$hidpi: min-resolution 1.5dppx;


//-----------------------------------------------------------


.container {
	padding-left: 15px;
	padding-right: 15px;

	@include container(980px);
}

@include breakpoint($mobile-2) {
	
	.container {
		padding-left: 10px;
		padding-right: 10px;
	}

}
