/* 
	-----------------------------------------------
		Навигация(хэдер)
		-------------------------------------------
		Стили брались из другой статьи с такой же
		навигацией, так что чуть отличаются
	-----------------------------------------------
*/

.b-menu__container-fluid:before,
.b-menu__row:before,
.b-menu__btn-holder:before,
.b-menu__nav:before,
.b-menu__container-fluid:after, 
.b-menu__row:after, 
.b-menu__btn-holder:after, 
.b-menu__nav:after {
	content: "";
  display: table;
}


.b-menu__container-fluid:after, 
.b-menu__row:after, 
.b-menu__btn-holder:after, 
.b-menu__nav:after {
	clear: both;
}



//---------------------------------------------
.b-menu {
  background-color: #fff;
  min-width: 320px;
  position: relative;
  height: 50px;
  z-index: 999; 
}


//---------------------------------------------
.b-menu__container-fluid {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 50px;
  background-color: #fff;
  z-index: 999;
  box-shadow: 0 3px 7px rgba(0, 0, 0, 0.15);
  margin-right: auto;
  margin-left: auto;
  padding-left: 32px;
  padding-right: 32px;
}

@media screen and (max-width: 480px) {
  .b-menu__container-fluid {
    margin-right: auto;
    margin-left: auto;
    padding-left: 10px;
    padding-right: 10px;
  }
}

//---------------------------------------------


.b-menu__row {
  margin-left: -10px;
  margin-right: -10px; 
}

.b-menu__header {
  height: 50px; 
}

.b-menu__header:before,
.b-menu__header:after {
  content: "";
  display: table; 
}

.b-menu__header:after {
  clear: both; 
}

.b-menu__logo {
  display: inline-block;
  padding: 13.5px 0px; 
}

.b-menu__toggle {
  position: relative;
  float: right;
  display: inline-block;
  background: #fff;
  border: none;
  overflow: hidden;
  font-size: 0;
  text-indent: -9999px;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  margin-right: -12.5px;
  width: 50px;
  height: 50px; 
  outline: none;
  cursor: pointer;
}

@media screen and (max-width: 480px) {
  .b-menu__toggle {
    margin-right: -10px; } 
}


.b-menu__toggle.active span {
  background: none; 
}

.b-menu__toggle.active span:before {
  top: 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg); 
}

.b-menu__toggle.active span:after {
  bottom: 0;
  -webkit-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  transform: rotate(-45deg); 
}

.b-menu__toggle.active span:before, .b-menu__toggle.active span:after {
  -webkit-transition-delay: 0s, 0.3s;
  transition-delay: 0s, 0.3s; 
}

.b-menu__icon-bar {
  position: absolute;
  top: 24px;
  left: 50%;
  margin-left: -12.5px;
  display: block;
  background: #5c5c5c;
  width: 25px;
  height: 2px;
  -webkit-transition: background 0s 0.3s;
          transition: background 0s 0.3s; 
}

.b-menu__icon-bar:before, .b-menu__icon-bar:after {
  content: '';
  position: absolute;
  left: 0;
  display: block;
  background-color: #5c5c5c;
  width: 100%;
  height: 2px;
  -webkit-transition-delay: 0.3s, 0s;
          transition-delay: 0.3s, 0s;
  -webkit-transition-duration: 0.3s, 0.3s;
          transition-duration: 0.3s, 0.3s; 
}


.b-menu__icon-bar:before {
  top: -8px;
  -webkit-transition-property: top, -webkit-transform;
  transition-property: top, transform; 
}


.b-menu__icon-bar:after {
    bottom: -8px;
    -webkit-transition-property: bottom, -webkit-transform;
    transition-property: bottom, transform; 
}


.b-menu__collapse {
  position: fixed;
  top: 50px;
  right: 0;
  bottom: 0;
  left: auto;
  background-color: #fff;
  -webkit-overflow-scrolling: touch;
  z-index: 998;
  width: 50%;
  -webkit-transition: all 0.6s;
  transition: all 0.6s;
  -webkit-transform: translateX(100%);
  -ms-transform: translateX(100%);
  transform: translateX(100%); 
}

@media screen and (max-width: 480px) {
  .b-menu__collapse {
    width: 83.33333%; } 
}

.b-menu__collapse.active {
  -webkit-transform: translateX(0);
  -ms-transform: translateX(0);
  transform: translateX(0);
  overflow-y: auto; 
}

.b-menu__block {
  padding-left: 30px;
  padding-right: 30px;
  background-color: #fff; 
}

@media screen and (max-width: 480px) {
  .b-menu__block {
    padding-left: 10px;
    padding-right: 10px; } 
}

.b-menu__btn-holder {
  padding-top: 20px;
  padding-bottom: 10px;
  margin-left: -10px;
  margin-right: -10px; 
}


@media screen and (max-width: 480px) {
  .b-menu__btn-holder {
    margin-left: -10px;
    margin-right: -10px; 
  } 
}

.b-menu__btn-holder a {
  display: block;
  background-color: #fff;
  color: #b51527;
  border: 1px solid #b51527;
  border-radius: 2px;
  font-size: 14px;
  line-height: 34px;
  height: 36px;
  text-align: center; 
}

.b-menu__btn-holder a:hover {
      background-color: #b51527;
      color: #fff; 
}

.b-menu__nav {
  margin: 0;
  padding: 0;
  list-style: none;
  margin-left: -10px;
  margin-right: -10px; 
}

@media screen and (max-width: 480px) {
  .b-menu__nav {
    margin-left: -10px;
    margin-right: -10px; 
  } 
}

.b-menu__column {
  float: left;
  width: 50%;
  padding-left: 10px;
  padding-right: 10px;

  a {
    text-decoration: none;
  }
}

.b-menu__column-title {
  position: relative;
  margin: 0;
  text-transform: uppercase;
  color: #464646;
  font-size: 14px;
  font-family: $bold;
  line-height: 24px;
  padding-bottom: 9px;
  margin-bottom: 7px; 
  font-weight: 700;
}

.b-menu__column-title:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  display: block;
  width: 42px;
  height: 3px;
  background-color: #b51527; 
}

.b-menu__item_list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.b-menu__item a {
  color: #464646;
  font-size: 14px;
  font-weight: 700;
  line-height: 30px; 
  text-decoration: none;
}

.b-menu__item a:hover, .b-menu__item a:focus {
  color: #b51527; 
}

.b-menu__backdrop {
  position: fixed;
  top: 50px;
  left: 0;
  right: 0;
  bottom: 0;
  visibility: hidden;
  background: rgba(0, 0, 0, 0.7);
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=0);
  opacity: 0;
  -webkit-transition: all 0.6s linear;
  transition: all 0.6s linear;
  z-index: 997; 
}

.b-menu__backdrop.active {
  filter: progid:DXImageTransform.Microsoft.Alpha(enabled=false);
  opacity: 1;
  visibility: visible; 
}