/* 
	-----------------------------------------------
		PRE CONTENT
	-----------------------------------------------
*/


.pre-content {
	padding-top: 30px;
	padding-bottom: 30px;

}



.pre-content__descr {
	margin-bottom: 15px;
	color: #616161;
	font-size: rem(18);
	font-weight: 400;
	text-align: center;
	line-height: 31px;
}




.pre-content__megaphone-pic {
	display: inline-block;
	width: 138px;
	height: 118px;

	background-image: url(../img/png/megaphone.png);
	background-repeat: no-repeat;
	background-size: contain;
	background-position: center;

	@include span(2);
}


.pre-content__hold-descr {
	font-size: rem(18);
	line-height: 31px;
	@include span(9);
}


@include breakpoint($mobile) {


	.pre-content {
		padding-bottom: 30px;
	}

	.pre-content__hold-descr {
		padding-top: 10px;
		@include span(12);
	}


	
	.pre-content__megaphone-pic,
	.pre-content__hold-descr {
		width: 100%;
	}

}
