/* 
	-----------------------------------------------
		ARTICLE HEADER
	-----------------------------------------------
*/

.article-header {
	padding-top: 35px;
	padding-bottom: 30px;
	background-color: #f0f1f2;
}

.article-header__title {
	margin-bottom: 50px;
	font-size: 3.875rem;
	font-weight: 700;
	color: #ff5a39;
	text-align: center;
}



.article-header__pic-wrap {
	text-align: center;
}

.article-header__pic {
	display: inline-block;
	width: percentage(506px / 980px);
	height: 360px;

	background-image: url(../img/png/header-pic.png);
	background-repeat: no-repeat;
	background-size: contain;
	background-position: center;
}


@include breakpoint($mobile) {

	.article-header__title {
		margin-bottom: 30px;
		font-size: rem(20);
	}

	.article-header__pic {
		height: 200px;
	}
}


@include breakpoint($mobile-2) {

	.article-header {
		padding-bottom: 0px;
	}


	.article-header__title {
		margin-bottom: 0px;
		font-size: rem(20);
	}

	.article-header__pic {
		height: 200px;
	}
}



