.content-article {
  font-family: $roboto;
  font-weight: 300;
  font-size: rem(18);
	p {
		margin-top: 0;
		margin-bottom: 25px;
	  line-height: 31px;
	}
}


@include breakpoint($mobile) {
	.content-article {
		p {
			margin-bottom: 20px;
		}
	}
}



//--------------------------------
.content-article__title {
	margin-bottom: 35px;
	font-family: $reg;
	font-size: rem(32px);
	font-weight: 700;
	color: #000;
}

.content-article__text-bold {
	font-size: rem(18);
	font-weight: 700;
	line-height: 31px;
}

.content-article__list {
	padding: 0;
	list-style: none;

	li {
		position: relative;
		margin-bottom: 20px;

		.dot {
			display: inline-block;
			min-width: 6px;
			width: 6px;
			height: 6px;
			margin-right: 10px;
			background-color: #323232;
			border-radius: 50%;
			vertical-align: middle;
			position: relative;
			top: -2px;
		}
	}
}


.content-article__note {
	clear: both;

	font-size: rem(18);
	line-height: 31px;

	background-color: #f7f7f7;
	padding: 25px 40px 30px 25px;
	border-left: 3px solid $red;

	&.note--m-bottom {
		margin-bottom: 25px;
	}
}


.content-article__link {
	color: $blue;
}


@include breakpoint($mobile) {

	.content-article__title {
		margin-bottom: 15px;
		font-size: rem(16);
	}
	.content-article__list {
		
		li {
			position: relative;
			margin-bottom: 10px;
		}
	}

	.content-article__note {

		padding: 10px;

		&.note--m-bottom {
			margin-bottom: 15px;
		}
	}


}




// Первый
//------------------------------------
.content-article__first {
	position: relative;
	margin-bottom: 50px;

	p {
		width: percentage(490px / 980px);
	}
}


.content-article__first-pic {
	float: right;
	width: percentage(282px / 980px);
	height: 282px;
	margin-right: 90px;


	background-image: url(../img/png/laptop.png);
	background-repeat: no-repeat;
	background-size: contain;
	background-position: center;

}


@include breakpoint($mobile) {


	.content-article__first {
		margin-bottom: 40px;

		p {
			width: 100%;
		}
	}

	.content-article__first-title {
		margin-bottom: 30px;
	}


	.content-article__first-pic {
		width: 100%;
		height: 200px;
		margin: 0 auto;
	}

}


@include breakpoint($mobile-2) {
		
	.content-article__first-pic {
		height: 160px;
	}
}



// Второй
//------------------------------------
.content-article__second {
	position: relative;
	margin-bottom: 50px;
}

.content-article__second-title {
	padding-right: 67px;
	text-align: right;
}

.content-article__second-wrap-test {
	margin-bottom: 25px;
	@include clearfix;
}
			
.content-article__second-pic {
	float: left;
	width: percentage(370px / 980px);
	height: 471px;
	margin-right: 10px;
	
	background-image: url(../img/png/rocket-start.png);
	background-repeat: no-repeat;
	background-size: contain;
	background-position: center;
}


@include breakpoint($mobile) {

	.content-article__second {
		margin-bottom: 40px;
	}


	.content-article__second-title {
		margin-bottom: 30px;
		padding-right: 0px;
		text-align: left;
	}


	.content-article__second-wrap-test {
		margin-bottom: 15px;
	}
			

	.content-article__second-pic {
		width: 100%;
		height: 160px;
		margin-bottom: 20px;
	}

}



