	padding-right: 20px;
}

.content-article__fourth-pic {
	float: right;
	width: percentage(390px / 980px);
	height: 402px;
	
	margin-top: 130px;


	background-image: url(../img/png/workspace.png);
	background-repeat: no-repeat;
	background-size: contain;
	background-position: center;
}


@include breakpoint($mobile) {
	
