/* 
	-----------------------------------------------
		FOOTER ARTICLE
	-----------------------------------------------
*/

.footer-article {
	
}


.footer-article__author {
	padding-top: 30px;
	padding-bottom: 25px;
	background-color: #f7f7f7;
	font-size: 16px;
	font-style: italic;
	text-align: center;
	color: #363636;
	a {
		font-size: 16px;
		color: $blue;
	}
}


.footer-article__voxlogo {
	padding-top: 14px;
	padding-bottom: 15px;
	background-color: #e2e2e2;
	.voxweb-logo {
		display: inline-block;
		vertical-align: middle;
		margin-right: 7px;

		svg {
			width: 22px;
			height: 22px;			
		}
	}

	a {
		color: #434343;
		font-size: 12px;
		font-weight: 300;
		vertical-align: middle;
	}
}


@include breakpoint($mobile-2) {

	.footer-article__author {
		padding-top: 30px;
		padding-bottom: 25px;
		background-color: #f7f7f7;
		font-size: 14px;

		a {
			font-size: 14px;
		}
		
	}
}