
* {
  box-sizing: border-box
}


body {
  min-width: 320px;
  position: relative;

  font-family: $reg;
  background-color: #fff;
  color: $black;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;

  &.open-menu {
    overflow: hidden;
  }

}

img {
  max-width: 100%;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

